// FEATURE BOX
.feature-bx1{
	padding: 50px 30px;
    border-radius: 0;
    background: #fff;
    margin-bottom: 30px;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
    -ms-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    position: relative;
    z-index: 1;
    text-align: center;
    border-radius: 20px;
    overflow: hidden;
}
.feature-bx1.active,
.feature-bx1:hover{
	transform:scale(1.05);
}
.feature-container.feature-bx1 .ld-tilte{
	margin-bottom:10px;
}
.feature-container.feature-bx1 p{
	font-size:18px;
	margin-bottom: 15px;
}
.feature-bx1 .btn-link{
	text-transform: uppercase;
	font-weight: 500;
	border-bottom: 2px solid;
	color: var(--bs-primary);
}
.feature-bx1 .btn-link:hover{
	text-decoration:none;
}
.feature-bx1 .icon-cell i{
	font-size:80px;
}
.feature-bx1 .bg-img{
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	z-index: -1;
	opacity: 0;
	transition:all 0.5s;
	-webkit-transition:all 0.5s;
	-ms-transition:all 0.5s;
	-moz-transition:all 0.5s;
	-o-transition:all 0.5s;
}
.feature-bx2 .feature-lg i,
.feature-bx1 .icon-cell{
	line-height: 80px;
}
.feature-bx1:hover,
.feature-bx1.active{
	box-shadow:0 10px 35px -15px var(--bs-primary);
	border-color:#fff;
	background:var(--bs-primary);
}
.feature-bx1.active .icon-cell,
.feature-bx1.active .btn-link,
.feature-bx1.active .ld-tilte,
.feature-bx1.active p,
.feature-bx1:hover .icon-cell,
.feature-bx1:hover .btn-link,
.feature-bx1:hover .ld-tilte,
.feature-bx1:hover p{
	color:#fff;
}
.feature-bx1.active .bg-img,
.feature-bx1:hover .bg-img{
	opacity:0.2;
}
.feature-bx1.active .icon-cell svg,
.feature-bx1:hover .icon-cell svg{
	fill: #fff;
}
.feature-bx1 .icon-cell{
	display:flex;
	height:100%;
	width:100%;
	align-items:center;
}
.feature-bx1 .icon-cell svg{
	width: 80px;
	height: 80px;
	fill: var(--bs-primary);
}

// FEATURE BOX
.feature-bx2{
	padding:50px 30px 80px 30px;
	border-radius:0;
	background:#fff;
	margin-bottom:30px;
	transition:all 0.5s;
	-webkit-transition:all 0.5s;
	-ms-transition:all 0.5s;
	-moz-transition:all 0.5s;
	-o-transition:all 0.5s;
	text-align:center;
	z-index:1;
	border-radius: 20px;
    box-shadow: 0 25px 50px 0 rgba(62, 9, 6, 0.05);
	
	.icon-content{
		overflow: unset;
		
		p{
			margin-bottom: 0;
			font-size: 18px;
			font-weight: 300;
			opacity: 0.8;
		}
	}
	.feature-lg i {
		font-size: 80px;
		line-height:80px;
		color:var(--bs-primary);
	}
	.feature-lg svg {
		width: 80px;
		height: 80px;
		fill: var(--bs-primary);	
	}
	
}

// FEATURE BOX 3
.feature-bx3{
	padding: 0 30px;
	position:relative;
}
.feature-bx3 .feature-box-xl svg {
	width: 60px;
    height: 60px;
	fill: var(--bs-primary);	
}
.feature-bx3 .feature-box-xl .icon-cell{
	display:flex;
	height:100%;
	width:100%;
	align-items:center;
	justify-content: center;
}
.feature-bx3 .feature-box-xl i {
    font-size: 60px;
}
.feature-bx3 .icon-content p{
	font-size: 18px;
	line-height: 28px;
}
.feature-bx3 .icon-content .ld-tilte{
	margin-bottom: 5px;
}
.feature-bx3 .icon-no{
	width: 40px;
	height: 40px;
	position: absolute;
	background: var(--bs-primary);
	border-radius: 40px;
	color:#fff !important;
	display: block;
	top: 0;
	right: 0;
	z-index: 5;
	line-height: 40px;
	font-weight:600;
}
