.feature-bx3.right-img2:after,
.feature-bx3.right-img:after{
	content: "";
    background-image: url(../images/arrow.png);
    width: 210px;
    height: 75px;
    position: absolute;
    right: -120px;
    top: 35px;
    opacity: 0.25;
	background-repeat: no-repeat;
	background-size: 100%;	
}
.feature-bx3.right-img2:after{
    transform: rotateX(180deg);
    -moz-transform: rotateX(180deg);
    -webkit-transform: rotateX(180deg);
    -ms-transform: rotateX(180deg);
    -o-transform: rotateX(180deg);
}
.bg-icon {
    color:#fff;
    position: absolute;
    bottom: 0px;
    right: 0px;
    z-index: -1;
    font-size: 200px;
    opacity: 0.05;
}
.feature-btn{
	width: 60px;
	height: 60px;
	position: absolute;
	background: var(--bs-primary);
	border-radius: 50px;
	color: #fff;
	font-size: 35px;
	line-height: 60px;
	bottom: -30px;
	left: calc(50% - 30px);	
	box-shadow: 0px 10px 40px -8px var(--bs-primary);	
	overflow: hidden;
}
.feature-bx2:hover .feature-btn i{
	-webkit-animation: moveUpIcon 0.5s forwards;
	-moz-animation: moveUpIcon 0.5s forwards;
	animation: moveUpIcon 0.5s forwards;
}
@-webkit-keyframes moveUpIcon {
	49% {
		-webkit-transform: translateX(100%);
	}
	50% {
		opacity: 0;
		-webkit-transform: translateX(-100%);
	}
	51% {
		opacity: 1;
	}
}
@-moz-keyframes moveUpIcon {
	49% {
		-moz-transform: translateX(100%);
	}
	50% {
		opacity: 0;
		-moz-transform: translateX(-100%);
	}
	51% {
		opacity: 1;
	}
}
.feature-bx2:hover{
    box-shadow: 0 25px 50px 0 rgba(62, 9, 6, 0.15);
	transform:translateY(-10px);
	-moz-transform:translateY(-10px);
	-webkit-transform:translateY(-10px);
	-ms-transform:translateY(-10px);
	-o-transform:translateY(-10px);
}
.feature-bx2:hover .feature-btn:after{
	bottom:0;
	top:auto;
	transform-origin:bottom;
	height:100%;
}
.feature-bx2:hover .feature-btn{
	color:#fff;
}
