.touch-count {
  position: relative;
  width: 120px;
}
.touch-count .touch-input {
  display: block;
  width: 100%;
  padding: 8px 35px;
  font-size: 0.9375rem;
  font-weight: 400;
  line-height: 1.334;
  color: var(--bs-body-color);
  appearance: none;
  background-color: var(--bs-body-bg);
  background-clip: padding-box;
  border: var(--bs-border-width) solid var(--bs-border-color);
  border-radius: var(--bs-border-radius);
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  text-align: center;
}
.touch-count .minus,
.touch-count .plus {
  position: absolute;
  z-index: 1;
  text-align: center;
  width: 28px;
  height: 28px;
  display: inline-flex;
  color: var(--bs-primary);
  background-color: var(--bs-primary-bg-subtle);
  justify-content: center;
  border-radius: 6px;
  border: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  transition: all 0.5s;
}
.touch-count .minus:hover,
.touch-count .plus:hover {
  color: #fff;
  background-color: var(--bs-primary);
}
.touch-count .minus {
  left: 5px;
}
.touch-count .plus {
  right: 5px;
}

.btn-slide-up-text {
  position: relative;
  overflow: hidden;
  display: inline-flex;
  align-items: center;
  transition: all 0.3s ease-in-out;
  justify-content: center;
}
.btn-slide-up-text .data-text {
  transition: opacity 0.6s, transform 0.8s;
  transition-timing-function: cubic-bezier(0.15, 0.85, 0.3, 1);
}
.btn-slide-up-text .data-text::before {
  content: attr(data-text);
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  white-space: nowrap;
  opacity: 0;
  transform: translate(-50%, 100%);
  transition: opacity 0.5s, transform 0.8s;
  transition-timing-function: cubic-bezier(0.15, 0.85, 0.3, 1);
}
.btn-slide-up-text:active .data-text, .btn-slide-up-text:hover .data-text {
  transform: translateY(-200%);
}
.btn-slide-up-text:active .data-text:before, .btn-slide-up-text:hover .data-text:before {
  opacity: 1;
  transform: translate(-50%, 150%);
}
.btn-slide-up-text:hover {
  transform: translate3d(0, -2px, 0);
}

