  position: absolute;
  top: 16px;
  left: -40px;
  width: 30px;
  height: 1px;
  background: var(--bs-primary);
}

.number-box:after {
  content: attr(data-name);
  position: absolute;
  font-size: 90px;
  font-weight: 700;
  color: #000;
  opacity: 0.1;
  left: 0;
  top: -10px;
  line-height: 90px;
  z-index: -1;
}

.number-box .btn-link {
  transform: translateY(30px);
  opacity: 0;
}

.number-box:hover .btn-link {
  transform: translateY(0);
  opacity: 1;
}

.number-box p {
  line-height: 24px;
  color: #6c6969;
  margin-bottom: 15px;
}

/* Split Box */
.split-box {
  position: relative;
  overflow: hidden;
  opacity: 0;
}

.split-box img {
  width: 100%;
}

.split-box > div {
  opacity: 0;
  -webkit-transition: 0s 0.8s;
  -o-transition: 0s 0.8s;
  transition: 0s 0.8s;
}

.split-box:before,
.split-box:after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.split-box:before {
  background-color: #fff;
  z-index: 1;
  -webkit-transform: translateX(-101%);
  -ms-transform: translateX(-101%);
  transform: translateX(-101%);
}

.split-box:after {
  background-color: var(--bs-primary);
  z-index: 2;
  -webkit-transform: translateX(-101%);
  -ms-transform: translateX(-101%);
  transform: translateX(-101%);
}

@-webkit-keyframes split-box-animation {
  0% {
    -webkit-transform: translateX(-101%);
    transform: translateX(-101%);
  }
  50% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(101%);
    transform: translateX(101%);
  }
}
@keyframes split-box-animation {
  0% {
    -webkit-transform: translateX(-101%);
    transform: translateX(-101%);
  }
  50% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(101%);
    transform: translateX(101%);
  }
}
.split-box.split-active,
.split-box.split-active > div {
  opacity: 1;
}

.split-box.split-active:after {
  -webkit-animation: split-box-animation 1.2s 0.2s cubic-bezier(1, 0, 0.54, 0.99) forwards;
  animation: split-box-animation 1.2s 0.2s cubic-bezier(1, 0, 0.54, 0.99) forwards;
}

.split-box.split-active:before {
  -webkit-animation: split-box-animation 1.5s cubic-bezier(0.86, 0, 0.07, 1) forwards;
  animation: split-box-animation 1.5s cubic-bezier(0.86, 0, 0.07, 1) forwards;
}

/* Split Box END */
